<template>
  <div>
    <mrc_ess_table v-if="$route.name=='staffmanagement'"
                   :headers='headers'
                   :paginationOptions='options'
                   :hideAddBtn="!hasBtn('add-staff')"
                   @turnPage='getlist'
                   :slotKey="['allstatus','name']"
                   :isLoading='loading'
                   :items='desserts'>
      <template v-slot:top>
        <v-card-title style="height:60px;position:relative;">
          <mrc_ess_searchInput :isShow='true'
                               style="position:absolute;right:10px;top:18px;"></mrc_ess_searchInput>
        </v-card-title>
      </template>
      <!-- 岗位职能 -->
      <template v-slot:item.name='{item}'>
        <mrc_ess_staffSpan :list='item'
                           class="mt-3 mb-3"></mrc_ess_staffSpan>
      </template>
      <!-- 状态 -->
      <template v-slot:item.allstatus='{item}'>
        <v-btn rounded
               color="#13a671"
               dark
               x-small
               v-if="item.STATUS==1">{{$t('label_To_enable_the')}}</v-btn>
        <v-btn rounded
               color="#999999"
               dark
               x-small
               v-if="item.STATUS==0">{{$t('label_Frozen')}}</v-btn>
        <v-btn rounded
               color="#a5d2e8"
               dark
               x-small
               v-if="item.staff_status==3">{{$t('label_Working')}}</v-btn>
        <v-btn rounded
               color="#ffe890"
               dark
               x-small
               v-if="item.staff_status==1">{{$t('label_Probation_period')}}</v-btn>
        <v-btn rounded
               color="#ffe890"
               dark
               x-small
               v-if="item.staff_status==2">{{$t('label_Get_ready_to_turn_right')}}</v-btn>
        <v-btn rounded
               color="#e5c2a2"
               dark
               x-small
               v-if="item.staff_status==6">{{$t('label_Retired')}}</v-btn>
        <v-btn rounded
               color="#ffddb9 "
               dark
               x-small
               v-if="item.staff_status==5">{{$t('label_Prepare_to_leave')}}</v-btn>
        <v-btn rounded
               color="#ffcff2"
               dark
               x-small
               v-if="item.staff_status==0">{{$t('label_Preparetojoin')}}</v-btn>
        <v-btn rounded
               color="#e3bec8"
               dark
               x-small
               v-if="item.staff_status==7">{{$t('label_Retired_office')}}</v-btn>
        <v-btn rounded
               color="#dfdfdf"
               dark
               x-small
               v-if="item.staff_status==4">{{$t('label_Stop')}}</v-btn>

      </template>
      <!-- 操作 -->
      <template v-slot:item.actions='{item}'>
        <menu-list :list='menulist'
                   @handleClick='handleClick($event,item)'>
          <template v-slot:showAction>
            <v-icon v-btnAuth="['view-staff-management',
                                'edit-staff-management',
                                'reset-staff-password',
                                'edit-staff-status']">mdi-dots-vertical</v-icon>
          </template>
        </menu-list>
      </template>
    </mrc_ess_table>

    <router-view v-else></router-view>
  </div>
</template>

<script>
import { validationMixin } from 'vuelidate'
// import { required, maxLength, email } from 'vuelidate/lib/validators'
import { required, maxLength, between, email, url } from 'vuelidate/lib/validators'
import { getStaffMsgList, freezeStaff, resetPassWord } from '@/service/api/staffmsg.js'
export default {
  components: {},
  props: {},
  data () {
    return {
      loading: false,
      options: {
        pagesize: 5,//条数
        cpage: this.$store.state.cpage,  //页数
        totalCount: null, //总条数
        totalpage: null, //总页数
      },
      desserts: [],
    };
  },
  created () { },
  async mounted () {
    await this.getlist()
  },
  beforeDestroy () {
    this.$store.commit('setCpage', 1)
  },
  methods: {
    async getlist (obj) {
      if (this.desserts.length == 1 && this.options.cpage != 1) this.options.cpage--
      const currentCompany = this.$store.state.currentCompany
      this.loading = true
      if (obj) {
        const { cpage, pagesize } = obj
        this.options = obj
      }
      const res = await getStaffMsgList({
        cpage: this.options.cpage,
        pagesize: this.options.pagesize,
        org_level_uuid: currentCompany
      })
      if (res.code === '200') {
        this.desserts = res.data
        this.options.totalCount = res.totalCount
        this.options.totalpage = res.totalpage
      }
      this.loading = false
    },
    godetail (item) {
      this.$store.commit('set_iconstatus', true)  //行内进图标显示
      this.$store.commit('set_subPage_name', (item.christian_name ? item.christian_name : '') + item.show_name)
      this.$store.commit('set_uuid', item.uuid)  //
      this.$store.commit('set_detailuuid', item.user_id)  //再用客户详情的detailuuid,暂时无bug
      this.$router.push({
        name: 'detailstaffmanagement'
      })
    },
    handleClick ({ index }, item) {
      switch (index) {
        case 0:
          this.godetail(item)
          break
        case 1:
          this.$store.commit('set_iconstatus', false)  //行内进图标显示 
          this.$store.commit('set_subPage_name', (item.christian_name ? item.christian_name : '') + item.show_name)
          this.$store.commit('set_uuid', item.uuid)  //
          this.$store.commit('set_detailuuid', item.user_id)
          this.$router.push({
            name: 'detailstaffmanagement'
          })
          break
        case 2:
          this.$myConfirm({ content: this.$t('message_Confirm_that_the_password_is_reset') }).then(res => {
            resetPassWord({ user_account: item.staff_email }).then(res => {
              if (res.code === '200') {
                this.$store.commit('newsTips', this.$t('message_The_operation_was_successful'))
                this.getlist()
              }
            })
          })
          break
        case 3:
          this.$myConfirm({ content: this.$t('message_Confirm_that_the_account_is_frozen') }).then(res => {
            freezeStaff({ uuid: item.uuid }).then(res => {
              if (res.code === '200') {
                this.$store.commit('newsTips', this.$t('message_The_operation_was_successful'))
                this.getlist()
              }
            })
          })
          break
      }
    }
  },
  computed: {
    menulist () {
      return [
        { name: this.$t('label_View'), show: this.hasBtn('view-staff-management') },
        { name: this.$t('label_Edit'), show: this.hasBtn('edit-staff-management') },
        { name: this.$t('label_To_reset_your_password'), show: this.hasBtn('reset-staff-password') },
        { name: this.$t('label_Frozen_account'), show: this.hasBtn('edit-staff-status') }
      ]
    },
    headers () {
      return [
        {
          text: this.$t('label_Employee_name') + '、' + this.$t('label_position_office') + '、' + this.$t('label_company') + '、' + this.$t('label_department'),
          align: 'start',
          value: 'name', sortable: false
        },
        { text: this.$t('label_account'), value: 'staff_email', sortable: false },
        { text: this.$t('label_state'), value: 'allstatus', sortable: false },
        { text: '', value: 'actions', sortable: false },
      ]
    },
  },
  watch: {
    $route (v) {
      if (v.name == 'staffmanagement') {
        this.getlist()
      }
    }
  },
};
</script>

<style scoped>
.sps1 {
  display: block;
  font-size: 16px;
}
.sps2 {
  display: block;
  color: #cccccc;
  /* word-break: keep-all;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis; */
}
.sps2 span:nth-child(1) {
  margin-right: 10px;
}
.sps2 span {
  max-width: 30vw;
  word-break: keep-all;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
</style>
